body {
  background-color: #161623;
  font-family: "Poppins", sans-serif;
}

body::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(#f00, #f0f);
  clip-path: circle(30% at 80% 52%);
}

body::after {
  content: "";
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(
    rgb(5, 182, 226),
    rgb(240, 48, 14),
    rgb(238, 75, 46)
  );
  clip-path: circle(25% at 10% 25%);
}

/*......................................Nav bar Design................................... */

.Container-1 {
  position: absolute;
  width: calc(100% - 32px);
  height: 0px;
  z-index: 3;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
  display: flex;
  padding: 30px;
  backdrop-filter: blur(20px);
  margin: 2%;
  margin-left: 1.5%;
  transition: 0.5s ease all;
}

.Container-1 ul {
  display: flex;
}

.Container-1 .logo {
  font-size: 1.5em;
  font-weight: 900;
  color: rgb(245, 133, 6);
  cursor: pointer;
}

.Container-1 .logo:hover {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 5px 5px 5px rgba(243, 247, 241, 0.5);
  padding: 9px;
  border-radius: 80px;
  backdrop-filter: blur(30px);
  text-transform: uppercase;
}

.Container-1 ul li {
  list-style: none;
  margin: 0 20px;
}

.Container-1 ul li .nav-bar-link {
  text-decoration: none;
  color: white;
  font-size: 1.5em;
  text-transform: capitalize;
}

.nav-bar-link:hover {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 5px 5px 5px rgba(243, 247, 241, 0.5);
  padding: 7px;
  border-radius: 20px;
  backdrop-filter: blur(30px);
  text-transform: uppercase;
  font-weight: 600;
}

