/*.............................. Grid Design different blocks....................................... */

.grid-item {
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  display: flex;
}

/* ..................................Grid Per Item Design.................................................. */

.item1 {
  height: auto;
  width: auto;
  grid-column: 1 / span 1;
  grid-row: 1 / span 16;
}
.item2 {
  height:100%;
  width: 100%;
  grid-column: 2;
  grid-row: 1;
  background:linear-gradient( 25deg,#7ee8fa,#eec0c6);
  background-color: white;   
}
.item3 {
  height: 100%;
  width: 100%;
  text-align: left;
  align-items: center;
  grid-column: 3;
  grid-row: 1 / span 23;
  overflow: scroll;
  overflow-x: hidden;
  display: block;
}
.item4 {
  height: 100%;
  width: 100%;
  grid-column: 1;
  grid-row: 17 / span 7;
  background: linear-gradient(90deg,rgba(54, 224, 236,0.2),rgba(212, 51, 233,0.2));
  display: flex;
  text-align: center;
  align-items: center;
}
.item5 {
  height: 100%;
  width: 100%;
  display: block;
  grid-column: 2 ;
  grid-row: 2 / span 22;
  overflow: scroll;
  background: linear-gradient(25deg,rgb(116, 231, 87,0.3),rgb(107, 209, 243,0.7));
  overflow-x: hidden;
  align-items: center;
  text-align: left;
  padding: 25px;
}

